

.bar{
    width: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: 1px 1px 5px #eee;
    border-radius: 5px;
    margin: 10px 0;
  }
  .range-wrap{
    display: flex;
  }
  .bar .type{
    font-size: 8px;
    text-transform: uppercase;
    padding: 2px 10px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }
  .range{
    width: 80%;
    position: relative
  }
  .range-text{
    position: relative;
    z-index: 5;
    padding: 5px 10px;
  }
  .status{
    width: 20%;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #eee
  }
  .range-line{
    z-index: 1;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    border-bottom-left-radius: 5px;
    transition: 1s cubic-bezier(.21,-0.01,.11,.85);
  }
  .range-ranks{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    height: 100%;
    width: 100%;
    z-index: 2
  }
  .range-ranks .range-40{
    width: 40%;
    border-right: 1px dashed #e6e6e6;
    mix-blend-mode: lighten;
    box-sizing: border-box;
  }
  .range-ranks .range-41{
    width: 40%;
    border-right: 1px dashed #e6e6e6;
    mix-blend-mode: lighten;
    box-sizing: border-box;
  }
  .range-ranks .range-60{
    width: 21%;
    border-right: 1px dashed #e6e6e6;
    mix-blend-mode: lighten;
    box-sizing: border-box;
  }
  .range-ranks .range-80{
    width: 20%;
    border-right: 1px dashed #e6e6e6;
    mix-blend-mode: lighten;
    box-sizing: border-box;
  }
  .range-ranks .range-20{
    width: calc( 20 / 30 * 100%);
    border-right: 1px dashed #e6e6e6;
    mix-blend-mode: lighten;
    box-sizing: border-box;
  }
.bar.exam .type{
    background: var(--exam);
    color: #fff;
}
.bar.kurs .type{
    background: var(--kurs);
    color: #fff;
}
.bar.za4 .type{
    background: var(--za4);
    color: #fff;
}
.bar.diff .type{
    background: var(--diff);
    color: #fff;
}
.bar.empty .type{
    background: var(--empty);
}
.range-line.fine{
    background-color: var(--fine);
}
.range-line.good{
  background-color: var(--good);
}
.range-line.bad{
  background-color: var(--bad);
}
.range-line.fail{
  background-color: var(--fail);
}
.range-line.empty{
  background-color: var(--empty);
}
.range-line.za4et{
  background-color: var(--za4et);
}
/* dark */
.bar.dark{
  box-shadow: 1px 1px 5px var(--secondary_color);
}
.bar.dark .status{
  border-left: 1px solid var(--secondary_color);
}
.bar.dark .range-line.fine{
  background-color: var(--fine_dark);
}
.bar.dark .range-line.good{
  background-color: var(--good_dark);
}
.bar.dark .range-line.bad{
  background-color: var(--bad_dark);
}
.bar.dark .range-line.fail{
  background-color: var(--fail_dark);
}
.bar.dark .range-line.empty{
  background-color: var(--empty_dark);
}
.bar.dark.exam .type{
  background: var(--exam_dark);
}
.bar.dark.kurs .type{
  background: var(--kurs_dark);
}
.bar.dark.za4 .type{
  background: var(--za4_dark);
}
.bar.dark.diff .type{
  background: var(--diff_dark);
}
.bar.dark.empty .type{
  background: var(--empty_dark);
}